ase */\n*, *::before, *::after {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\n:root {\n  --color-primary: #3B82F6;\n  --color-primary-dark: #2563EB;\n  --color-primary-light: #60A5FA;\n  --color-secondary: #10B981;\n  --color-danger: #EF4444;\n  --color-warning: #F59E0B;\n  --color-success: #10B981;\n  \n  --color-bg: #F3F4F6;\n  --color-bg-dark: #1F2937;\n  --color-surface: #FFFFFF;\n  --color-surface-dark: #374151;\n  \n  --color-text: #1F2937;\n  --color-text-light: #6B7280;\n  --color-text-dark: #F9FAFB;\n  \n  --color-border: #E5E7EB;\n  --color-border-dark: #4B5563;\n  \n  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);\n  --shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);\n  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);\n  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);\n  \n  --radius-sm: 0.25rem;\n  --radius: 0.375rem;\n  --radius-md: 0.5rem;\n  --radius-lg: 0.75rem;\n  \n  --font-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;\n  --font-mono: 'SF Mono', Consolas, 'Liberation Mono', Menlo, monospace;\n}\n\nbody {\n  font-family: var(--font-sans);\n  background-color: var(--color-bg);\n  color: var(--color-text);\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n/* Layout */\n.app {\n  display: flex;\n  flex-direction: column;\n  min-height: 100vh;\n}\n\n.header {\n  background: var(--color-surface);\n  border-bottom: 1px solid var(--color-border);\n  padding: 0.75rem 1rem;\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n  box-shadow: var(--shadow-sm);\n}\n\n.header-title {\n  font-size: 1.25rem;\n  font-weight: 600;\n  color: var(--color-primary);\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n}\n\n.header-nav {\n  display: flex;\n  gap: 0.5rem;\n  margin-left: auto;\n}\n\n.main {\n  display: flex;\n  flex: 1;\n  overflow: hidden;\n}\n\n.sidebar {\n  width: 280px;\n  background: var(--color-surface);\n  border-right: 1px solid var(--color-border);\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n}\n\n.sidebar-header {\n  padding: 1rem;\n  border-bottom: 1px solid var(--color-border);\n  font-weight: 600;\n}\n\n.sidebar-content {\n  flex: 1;\n  overflow-y: auto;\n  padding: 0.5rem;\n}\n\n.content {\n  flex: 1;\n  overflow: auto;\n  padding: 1.5rem;\n}\n\n/* Buttons */\n.btn {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  gap: 0.5rem;\n  padding: 0.5rem 1rem;\n  font-size: 0.875rem;\n  font-weight: 500;\n  border-radius: var(--radius);\n  border: 1px solid transparent;\n  cursor: pointer;\n  transition: all 0.15s ease;\n}\n\n.btn:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\n.btn-primary {\n  background: var(--color-primary);\n  color: white;\n}\n\n.btn-primary:hover:not(:disabled) {\n  background: var(--color-primary-dark);\n}\n\n.btn-secondary {\n  background: var(--color-surface);\n  color: var(--color-text);\n  border-color: var(--color-border);\n}\n\n.btn-secondary:hover:not(:disabled) {\n  background: var(--color-bg);\n}\n\n.btn-danger {\n  background: var(--color-danger);\n  color: white;\n}\n\n.btn-danger:hover:not(:disabled) {\n  background: #DC2626;\n}\n\n.btn-icon {\n  padding: 0.5rem;\n  border-radius: var(--radius);\n}\n\n.btn-sm {\n  padding: 0.25rem 0.5rem;\n  font-size: 0.75rem;\n}\n\n.btn-lg {\n  padding: 0.75rem 1.5rem;\n  font-size: 1rem;\n}\n\n/* Forms */\n.form-group {\n  margin-bottom: 1rem;\n}\n\n.form-label {\n  display: block;\n  margin-bottom: 0.25rem;\n  font-size: 0.875rem;\n  font-weight: 500;\n  color: var(--color-text);\n}\n\n.form-input {\n  width: 100%;\n  padding: 0.5rem 0.75rem;\n  font-size: 0.875rem;\n  border: 1px solid var(--color-border);\n  border-radius: var(--radius);\n  background: var(--color-surface);\n  transition: border-color 0.15s ease, box-shadow 0.15s ease;\n}\n\n.form-input:focus {\n  outline: none;\n  border-color: var(--color-primary);\n  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);\n}\n\n.form-select {\n  appearance: none;\n  background-image: url(\"data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e\");\n  background-position: right 0.5rem center;\n  background-repeat: no-repeat;\n  background-size: 1.5em 1.5em;\n  padding-right: 2.5rem;\n}\n\n/* Cards */\n.card {\n  background: var(--color-surface);\n  border: 1px solid var(--color-border);\n  border-radius: var(--radius-md);\n  box-shadow: var(--shadow);\n}\n\n.card-header {\n  padding: 1rem;\n  border-bottom: 1px solid var(--color-border);\n  font-weight: 600;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.card-body {\n  padding: 1rem;\n}\n\n.card-footer {\n  padding: 1rem;\n  border-top: 1px solid var(--color-border);\n  background: var(--color-bg);\n}\n\n/* Tables */\n.table {\n  width: 100%;\n  border-collapse: collapse;\n  font-size: 0.875rem;\n}\n\n.table th,\n.table td {\n  padding: 0.75rem;\n  text-align: left;\n  border-bottom: 1px solid var(--color-border);\n}\n\n.table th {\n  font-weight: 600;\n  background: var(--color-bg);\n  color: var(--color-text-light);\n  text-transform: uppercase;\n  font-size: 0.75rem;\n  letter-spacing: 0.05em;\n}\n\n.table tbody tr:hover {\n  background: var(--color-bg);\n}\n\n.table-compact th,\n.table-compact td {\n  padding: 0.5rem;\n}\n\n/* Pool Grid */\n.pool-grid {\n  display: inline-block;\n  border: 2px solid var(--color-border);\n  border-radius: var(--radius-md);\n  overflow: hidden;\n}\n\n.pool-row {\n  display: flex;\n}\n\n.pool-cell {\n  width: 48px;\n  height: 48px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border: 1px solid var(--color-border);\n  font-weight: 500;\n}\n\n.pool-cell-header {\n  background: var(--color-bg);\n  font-weight: 600;\n}\n\n.pool-cell-name {\n  width: 150px;\n  justify-content: flex-start;\n  padding-left: 0.5rem;\n}\n\n.pool-cell-diagonal {\n  background: #374151;\n}\n\n.pool-cell-victory {\n  background: #D1FAE5;\n  color: #065F46;\n}\n\n.pool-cell-defeat {\n  background: #FEE2E2;\n  color: #991B1B;\n}\n\n.pool-cell-editable {\n  cursor: pointer;\n}\n\n.pool-cell-editable:hover {\n  background: var(--color-primary-light);\n  color: white;\n}\n\n/* Tableau (Direct Elimination) */\n.tableau-container {\n  overflow: auto;\n  padding: 1rem;\n}\n\n.tableau {\n  display: flex;\n  gap: 2rem;\n  min-width: max-content;\n}\n\n.tableau-round {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n}\n\n.tableau-round-header {\n  text-align: center;\n  font-weight: 600;\n  padding: 0.5rem;\n  background: var(--color-bg);\n  border-radius: var(--radius);\n  margin-bottom: 0.5rem;\n}\n\n.tableau-match {\n  background: var(--color-surface);\n  border: 1px solid var(--color-border);\n  border-radius: var(--radius);\n  overflow: hidden;\n  min-width: 200px;\n}\n\n.tableau-fencer {\n  padding: 0.5rem;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  border-bottom: 1px solid var(--color-border);\n}\n\n.tableau-fencer:last-child {\n  border-bottom: none;\n}\n\n.tableau-fencer-winner {\n  background: #D1FAE5;\n  font-weight: 600;\n}\n\n.tableau-fencer-name {\n  font-size: 0.875rem;\n}\n\n.tableau-fencer-score {\n  font-weight: 600;\n  min-width: 24px;\n  text-align: center;\n}\n\n/* Status badges */\n.badge {\n  display: inline-flex;\n  align-items: center;\n  padding: 0.125rem 0.5rem;\n  font-size: 0.75rem;\n  font-weight: 500;\n  border-radius: 9999px;\n}\n\n.badge-success {\n  background: #D1FAE5;\n  color: #065F46;\n}\n\n.badge-warning {\n  background: #FEF3C7;\n  color: #92400E;\n}\n\n.badge-danger {\n  background: #FEE2E2;\n  color: #991B1B;\n}\n\n.badge-info {\n  background: #DBEAFE;\n  color: #1E40AF;\n}\n\n/* 