// ============================================================================
// Enums
// ============================================================================

export enum Weapon {
  EPEE = 'E',
  FOIL = 'F',
  SABRE = 'S',
  LASER = 'L',  // Sabre Laser
}

export enum Gender {
  MALE = 'M',
  FEMALE = 'F',
  MIXED = 'X',
}

export enum FencerStatus {
  QUALIFIED = 'Q',         // Qualifi
  ELIMINATED = 'E',        // limin
  ABANDONED = 'A',         // Abandon
  EXCLUDED = 'X',          // Exclu (carton noir)
  NOT_CHECKED_IN = 'N',    // Non point
  CHECKED_IN = 'P',        // Point (prsent)
  FORFAIT = 'F',           // Forfait
}

export enum MatchStatus {
  NOT_STARTED = 'not_started',
  IN_PROGRESS = 'in_progress',
  FINISHED = 'finished',
  CANCELLED = 'cancelled',
}

export enum PhaseType {
  CHECKIN = 'checkin',
  POOL = 'pool',
  DIRECT_ELIMINATION = 'direct_elimination',
  CLASSIFICATION = 'classification',
}

export enum Category {
  U11 = 'U11',      // Poussins
  U13 = 'U13',      // Benjamins
  U15 = 'U15',      // Minimes
  U17 = 'U17',      // Cadets
  U20 = 'U20',      // Juniors
  SENIOR = 'SEN',   // Seniors
  V1 = 'V1',        // Vtrans 1 (40-49)
  V2 = 'V2',        // Vtrans 2 (50-59)
  V3 = 'V3',        // Vtrans 3 (60-69)
  V4 = 'V4',        // Vtrans 4 (70+)
}

// ============================================================================
// Base Types
// ============================================================================

export interface BaseEntity {
  id: string;
  createdAt: Date;
  updatedAt: Date;
}

// ============================================================================
// Fencer (Tireur)
// ============================================================================

export interface Fencer extends BaseEntity {
  ref: number;                    // Numro de rfrence unique dans la comptition
  lastName: string;               // Nom
  firstName: string;              // Prnom
  birthDate?: Date;               // Date de naissance
  gender: Gender;                 // Sexe
  nationality: string;            // Nation (code ISO)
  league?: string;                // Ligue/Rgion
  club?: string;                  // Club
  license?: string;               // Numro de licence
  ranking?: number;               // Classement
  status: FencerStatus;           // Statut dans la comptition
  seedNumber?: number;            // Tte de srie
  initialRanking?: number;        // Classement initial
  finalRanking?: number;          // Classement final
  
  // Stats calcules dans les poules
  poolStats?: PoolStats;
}

export interface PoolStats {
  victories: number;              // Victoires
  defeats: number;                // Dfaites
  touchesScored: number;          // Touches donnes (TD)
  touchesReceived: number;        // Touches reues (TR)
  index: number;                  // Indice (TD - TR)
  matchesPlayed: number;          // Matchs jous
  victoryRatio: number;           // V/M (ratio victoires/matchs)
  poolRank?: number;              // Rang dans la poule
  overallRank?: number;           // Rang gnral aprs poules
}

// ============================================================================
// Referee (Arbitre)
// ============================================================================

export interface Referee extends BaseEntity {
  ref: number;
  lastName: string;
  firstName: string;
  birthDate?: Date;
  gender: Gender;
  nationality: string;
  league?: string;
  license?: string;
  category?: string;              // Niveau d'arbitrage (Rgional, National, International)
  status: 'available' | 'assigned' | 'unavailable';
}

// ============================================================================
// Score
// ============================================================================

export interface Score {
  value: number | null;           // Score numrique ou null si non renseign
  isVictory: boolean;             // V pour victoire
  isAbstention: boolean;          // A pour abstention/abandon
  isExclusion: boolean;           // X pour exclusion (carton noir)
  isForfait: boolean;             // F pour forfait
}

// ============================================================================
// Match
// ============================================================================

export interface Match extends BaseEntity {
  number: number;                 // Numro du match
  fencerA: Fencer | null;         // Premier tireur
  fencerB: Fencer | null;         // Deuxime tireur
  scoreA: Score | null;           // Score du tireur A
  scoreB: Score | null;           // Score du tireur B
  maxScore: number;               // Score maximum (5 en poule, 10/15 en tableau)
  status: MatchStatus;
  referee?: Referee;              // Arbitre assign
  strip?: number;                 // Piste
  startTime?: Date;               // Heure de dbut prvue
  endTime?: Date;                 // Heure de fin
  duration?: number;              // Dure en secondes
  poolId?: string;                // ID de la poule (si match de poule)
  tableId?: string;               // ID du tableau (si match de tableau)
  round?: number;                 // Tour du tableau (64, 32, 16, 8, 4, 2, 1)
  position?: number;              // Position dans le tour
}

// ============================================================================
// Pool (Poule)
// ============================================================================

export interface Pool extends BaseEntity {
  number: number;                 // Numro de la poule
  phaseId: string;                // ID de la phase de poules
  fencers: Fencer[];              // Tireurs dans la poule
  matches: Match[];               // Matchs de la poule
  referees: Referee[];            // Arbitres assigns
  strip?: number;                 // Piste assigne
  startTime?: Date;               // Heure de dbut
  isComplete: boolean;            // Tous les matchs termins
  hasError: boolean;              // Erreur dtecte dans les scores
  ranking: PoolRanking[];         // Classement calcul
}

export interface PoolRanking {
  fencer: Fencer;
  rank: number;
  victories: number;
  defeats: number;
  touchesScored: number;
  touchesReceived: number;
  index: number;
  ratio: number;
  // Points Quest (Sabre Laser uniquement)
  questPoints?: number;              // Total des points Quest
  questVictories4?: number;          // Nombre de victoires  4 points (cart 12)
  questVictories3?: number;          // Nombre de victoires  3 points (cart 8-11)
  questVictories2?: number;          // Nombre de victoires  2 points (cart 4-7)
  questVictories1?: number;          // Nombre de victoires  1 point (cart 3)
}

// ============================================================================
// Pool Phase (Tour de poules)
// ============================================================================

export interface PoolPhase extends BaseEntity {
  competitionId: string;
  phaseNumber: number;            // Numro du tour de poules (1, 2, etc.)
  maxScore: number;               // Score max par match (gnralement 5)
  pools: Pool[];                  // Liste des poules
  config: PoolPhaseConfig;        // Configuration
  qualifiedCount?: number;        // Nombre de qualifis pour la phase suivante
  isComplete: boolean;
  ranking: PoolRanking[];         // Classement gnral aprs ce tour
}

export interface PoolPhaseConfig {
  minPoolSize: number;            // Taille minimum des poules (dfaut: 5)
  maxPoolSize: number;            // Taille maximum des poules (dfaut: 8)
  balanced: boolean;              // quilibrer les poules
  seeding: 'serpentine' | 'sequential' | 'random';  // Mthode de rpartition
  separation: {
    byClub: boolean;              // Sparer par club
    byLeague: boolean;            // Sparer par ligue
    byNation: boolean;            // Sparer par nation
  };
}

// ============================================================================
// Direct Elimination Table (Tableau)
// ============================================================================

export interface TableNode extends BaseEntity {
  position: number;               // Position dans le tableau (0 = finale)
  round: number;                  // Tour (1=finale, 2=demi, 4=quart, etc.)
  match?: Match;                  // Match  ce noeud
  winner?: Fencer;                // Gagnant qui avance
  fencerA?: Fencer;               // Tireur haut
  fencerB?: Fencer;               // Tireur bas
  parentA?: string;               // ID du noeud parent haut
  parentB?: string;               // ID du noeud parent bas
  isBye: boolean;                 // Exempt (avance directement)
}

export interface DirectEliminationTable extends BaseEntity {
  competitionId: string;
  name: string;                   // "Tableau principal", "3me place", etc.
  size: number;                   // Taille du tableau (64, 32, 16, etc.)
  maxScore: number;               // Score max (10 ou 15)
  nodes: TableNode[];             // Structure arborescente
  isComplete: boolean;
  ranking: TableRanking[];        // Classement final
  firstPlace: number;             // Premire place couverte (1, 5, 9, etc.)
}

export interface TableRanking {
  fencer: Fencer;
  rank: number;
  eliminatedAt: number;           // Tour d'limination
}

// ============================================================================
// Competition (Comptition)
// ============================================================================

export interface Competition extends BaseEntity {
  // Informations gnrales
  title: string;                  // Titre long
  shortTitle?: string;            // Titre court
  date: Date;                     // Date de la comptition
  location?: string;              // Lieu
  organizer?: string;             // Organisateur
  organizerUrl?: string;          // Site web de l'organisateur
  
  // Configuration
  weapon: Weapon;                 // Arme
  gender: Gender;                 // Sexe
  category: Category;             // Catgorie d'ge
  championship?: string;          // Type de championnat (FFE, FIE, etc.)
  color: string;                  // Couleur associe (hex)
  
  // Horaires
  checkInTime?: Date;             // Heure d'appel
  scratchTime?: Date;             // Heure de scratch
  startTime?: Date;               // Heure de dbut
  
  // Participants
  fencers: Fencer[];              // Liste des tireurs
  referees: Referee[];            // Liste des arbitres
  
  // Phases
  phases: Phase[];                // Phases de la comptition
  currentPhaseIndex: number;      // Phase en cours
  
  // Paramtres
  settings: CompetitionSettings;
  
  // tat
  isTeamEvent: boolean;           // Comptition par quipes
  status: 'draft' | 'in_progress' | 'completed' | 'cancelled';
}

export interface CompetitionSettings {
  defaultPoolMaxScore: number;    // Score max en poules (dfaut: 5)
  defaultTableMaxScore: number;   // Score max en tableau (dfaut: 10 ou 15)
  poolRounds: number;             // Nombre de tours de poules (dfaut: 1)
  hasDirectElimination: boolean;  // Phase d'limination directe active (dfaut: true)
  thirdPlaceMatch: boolean;       // Match pour la 3me place activ (dfaut: false)
  manualRanking: boolean;         // Classement manuel
  defaultRanking: number;         // Classement par dfaut pour non-classs
  randomScore: boolean;           // Scores alatoires (pour tests)
  minTeamSize: number;            // Taille min quipe (comptitions par quipes)
}

export interface Phase extends BaseEntity {
  competitionId: string;
  type: PhaseType;
  order: number;                  // Ordre dans la comptition
  name: string;
  isComplete: boolean;
  nextPhaseId?: string;           // ID de la phase suivante
  config: PoolPhaseConfig | DirectEliminationConfig | CheckInConfig;
}

export interface DirectEliminationConfig {
  maxScore: number;
  placesToFence: number[];        // Places  tirer (ex: [1, 3, 5, 7] pour 8)
  thirdPlaceMatch: boolean;       // Match pour la 3me place
}

export interface CheckInConfig {
  allowLateRegistration: boolean;
  autoQualify: boolean;
}

// ============================================================================
// Piste/Strip Configuration
// ============================================================================

export interface Strip {
  id: string;
  number: number;
  name?: string;
  isAvailable: boolean;
  currentMatch?: Match;
}

// ============================================================================
// Import/Export Types
// ============================================================================

export interface ImportResult {
  success: boolean;
  fencersImported: number;
  refereesImported: number;
  errors: string[];
  warnings: string[];
}

export interface ExportFormat {
  type: 'xml' | 'csv' | 'json' | 'pdf' | 'html';
  includeResults: boolean;
  includeStats: boolean;
}

// ============================================================================
// UI State Types
// ============================================================================

export interface AppState {
  currentCompetition: Competition | null;
  competitions: Competition[];
  selectedPhase: Phase | null;
  selectedPool: Pool | null;
  selectedTable: DirectEliminationTable | null;
  isLoading: boolean;
  error: string | null;
}

export interface UISettings {
  language: 'fr' | 'en' | 'de' | 'es' | 'nl';
  theme: 'light' | 'dark' | 'system';
  fontSize: 'small' | 'medium' | 'large';
  showTips: boolean;
  autoSave: boolean;
  autoSaveInterval: number;       // En secondes
}
